import React from 'react'
import { Link, useNavigate } from 'react-router-dom';
import "../../styles/Header.css"
import { useSelector } from 'react-redux';
import { adminMenu, userMenu } from '../../data/data';
import { message } from 'antd';

const Header = () => {
    const navigate = useNavigate();
    const { user } = useSelector(state => state.user)

    const logoutHandler = () => {
        localStorage.clear();
        navigate("/");
        message.success("Logout Successfully");
        window.location.reload();
    }
    // ----------------- Doctor Menu ------------------
    const doctorMenu = [
        {
            name: "Appointment",
            path: `/appointment/${user?._id}`,
            icon: "fa-solid fa-list"
        },
        {
            name: "Profile",
            path: `/doctor/profile/${user?._id}`,
            icon: "fa-solid fa-user"
        },
        {
            name: "Support",
            path: '/support',
            icon: "fa-solid fa-user"
        },
    ]
    // ----------------- Doctor Menu ------------------

    const siderMenu = user?.isAdmin ? adminMenu : user?.isDoctor ? doctorMenu : userMenu;

    return (
        <>
            <nav className="navbar navbar-expand-lg bg-white">
                <div className="container-fluid">
                    <Link className="navbar-brand logo ps-1 ps-md-1 ps-lg-5" to="/"><span className='fw-bold'>credi</span>health</Link>
                    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                        <span className="navbar-toggler-icon"></span>
                    </button>
                    <div className="collapse navbar-collapse" id="navbarSupportedContent">

                        {user &&
                            <ul className="navbar-nav me-auto mb-2 mb-lg-0 d-block d-md-none d-lg-none top-header-right lg-ms-5 ms-0">
                                {
                                    siderMenu.map((menu) => {
                                        return <li li className="nav-item ms-lg-5 ms-0">
                                            <Link className="nav-link m-2 my-3" aria-current="page" to={menu.path}>
                                                {menu.name}
                                            </Link>
                                        </li>
                                    })
                                }
                            </ul>
                        }
                        <hr className='my-3 p-0' />
                        <div className="inputLocation d-none d-md-block d-lg-block">
                            <i className="fa-solid fa-location-dot"></i>
                            <input type="text" placeholder='Choose City' />
                        </div>
                        <div className="inputSearchOptions d-none d-md-block d-lg-block">
                            <i class="fa-sharp fa-solid fa-magnifying-glass"></i>
                            <input type="text" placeholder='Search Doctor, Hospitals, Speciality or Treatment' />
                        </div>

                        <ul className="navbar-nav me-auto mb-2 mb-lg-0 top-header-right lg-ms-5 ms-0">
                            <li className="nav-item ms-lg-5 ms-0">
                                <Link className="nav-link m-2" aria-current="page" href="#">
                                    Write to Us
                                    <i className="ms-2 fa-solid fa-pen-to-square"></i>
                                </Link>
                            </li>
                            <li className="nav-item mb-4 mb-lg-0">
                                <Link className="nav-link m-2" aria-current="page" href="#">
                                    Services
                                    <i className="ms-2 fa-solid fa-chevron-down"></i>
                                </Link>
                            </li>
                            {
                                user ?
                                    <button className='loginBtn' onClick={logoutHandler}>{user.firstName}<i class="ms-2 fa-solid fa-right-from-bracket"></i></button>
                                    :
                                    <button className='loginBtn' onClick={() => navigate('/login')}><i className='me-2 fa-regular fa-user'></i>
                                        Login
                                    </button>
                            }
                        </ul>
                    </div>
                </div>
            </nav>
        </>

    )
}

export default Header
