import React from 'react';
import "../styles/Layout.css";
import { adminMenu, userMenu } from '../data/data';
import { Link, useLocation, useNavigate } from "react-router-dom";
import { useSelector } from 'react-redux';
import { message } from 'antd';
import Header from './Header/Header';
import Footer from './Footer/Footer';

const Layout = ({ children }) => {

    const location = useLocation();
    const navigate = useNavigate();
    const { user } = useSelector(state => state.user);

    const logoutHandler = () => {
        localStorage.clear();
        navigate("/");
        message.success("Logout Successfully");
        window.location.reload();
    }

    // ----------------- Doctor Menu ------------------
    const doctorMenu = [
        {
            name: "Appointment",
            path: `/appointment/${user?._id}`,
            icon: "fa-solid fa-list"
        },
        {
            name: "Profile",
            path: `/doctor/profile/${user?._id}`,
            icon: "fa-solid fa-user"
        },
        {
            name: "Support",
            path: "/support",
            icon: "fa-solid fa-user"
        },
    ]
    // ----------------- Doctor Menu ------------------

    const siderMenu = user?.isAdmin ? adminMenu : user?.isDoctor ? doctorMenu : userMenu;

    return (
        <>
            <div className="main">
                <Header logout={logoutHandler} />
                <div className="layout p-lg-4 p-0 py-4">
                    <div className='sidebarContainer'>
                        <div className="sidebar d-md-block d-lg-block d-none">
                            <div className="profileContainer">
                                <div className='profile'>
                                    <img src="https://www.linkpicture.com/q/pngwing.com-1_15.png" width={80} alt="" />
                                    <div className='userName'>
                                        <p>Hello,</p>
                                        <p>{user?.firstName}</p>
                                    </div>
                                </div>
                                <div className='notification' style={{ cursor: "pointer" }} onClick={() => navigate('/notification')}>
                                    <div>
                                        <i className="fa-solid fa-bell"></i>
                                    </div>
                                    <span class="badge bg-danger">{user && user.notification.length}</span>
                                </div>
                            </div>
                        </div>

                        <div className="sidebar d-md-block d-lg-block d-none p-2 mt-2">
                            <div className="menu">
                                {siderMenu.map((menu) => {
                                    const isActive = location.pathname === menu.path;
                                    return (
                                        <>
                                            <div className={`menu-item ${isActive && "active"}`}>
                                                <i className={menu.icon}></i>
                                                <Link to={menu.path}>{menu.name}</Link>
                                            </div>
                                            <hr className='m-0 p-0' />
                                        </>
                                    )
                                })}
                                <div className={`menu-item`} onClick={logoutHandler}>
                                    <i className="fa-solid fa-right-from-bracket"></i>
                                    <Link to="/login">Logout</Link>
                                </div>
                            </div>
                        </div>
                    </div>

                    {/* ----------------- Mobile Menu ---------------------- */}

                    <div className="content container-fluid">
                        <div className="row">
                            <div className="col-12 col-md-12 col-lg-12 col-12">
                                <div className="body">{children}</div>
                            </div>
                        </div>
                    </div>
                </div>
                <Footer />
            </div>
        </>
    )
}

export default Layout
