import { message } from 'antd';
import axios from 'axios';
import React, { useEffect, useState } from 'react'
import Layout from '../../components/Layout';
import { useNavigate, useParams } from 'react-router-dom';
const DoctorAppointmentPage = () => {
    const navigate = useNavigate();
    const params = useParams();
    const [appoinments, setAppointments] = useState([])
    const [date, setDate] = useState('');

    const getDoctorAppointments = async () => {
        try {
            const res = await axios.post('/api/v1/doctor/getAllDoctorAppointments',
                {
                    userId: params.doctorId,
                },
                {
                    headers: {
                        Authorization: `Bearer ${localStorage.getItem("token")}`
                    }
                })
            if (res.data.success) {
                setAppointments(res.data.data);
            } else {
                message.error(res.data.message)
            }
        } catch (error) {
            console.log(error);
            message.error("Something went wrong")
        }
    }

    const handleApprove = async (id) => {
        try {
            const res = await axios.post('/api/v1/doctor/doctorApproveAppointment',
                {
                    appointmentId: id
                },
                {
                    headers: {
                        Authorization: `Bearer ${localStorage.getItem("token")}`
                    }
                }
            )
            if (res.status.success) {
                message.success(res.data.message);
                navigate('/')
            } else {
                message.error(res.data.message)
            }
        } catch (error) {
            message.error("Something went wrong")
        }
    }

    const allAppointmentHandler = async () => {
        setDate('')
    }

    useEffect(() => {
        getDoctorAppointments();
    }, [])

    return (
        <Layout>
            <div className="d-lg-flex justify-content-end my-4 text-center">
                <p className='text-secondary mx-2'>Filter Appoinments by Date</p>
                <div>
                    <input style={{
                        borderRadius: "5px", border: "1px solid grey", padding: "2px"
                    }} type="date" value={date} onChange={(e) => setDate(e.target.value)} />
                    <i style={{ cursor: "pointer" }} onClick={allAppointmentHandler} class="fa-solid fa-arrows-rotate mx-2"></i>
                </div>
            </div>
            <div className="bg-dark p-2 rounded my-4">
                <h3 className='text-white text-center pt-2'>APPOINTMENT LISTS
                    <i className='ms-3 fa-solid fa-user text-white'></i>
                </h3>
            </div>
            <table class="table">
                <thead>
                    <tr>
                        <th scope="col">Sr no.</th>
                        <th scope="col">Patient Name</th>
                        <th scope="col">Email</th>
                        <th scope="col">Date</th>
                        <th scope="col">Status</th>
                    </tr>
                </thead>
                <tbody>
                    {
                        appoinments.length > 0 ? appoinments.reverse().filter((elm) => elm.date.includes(date)).map((appointment) => {
                            return (
                                <tr>
                                    <th scope="row">{appoinments.indexOf(appointment) + 1}</th>
                                    <td>{(appointment.userInfo.name)}</td>
                                    <td>{(appointment.userInfo.email)}</td>
                                    <td>{(appointment.date)}</td>
                                    <td>{(appointment.status)}</td>
                                </tr>
                            )
                        }) : <h5>No Record Found</h5>
                    }
                </tbody>
            </table>

        </Layout>
    )
}

export default DoctorAppointmentPage
