import { message } from 'antd'
import axios from 'axios'
import React, { useEffect, useState } from 'react'
import Layout from "../../components/Layout"
import "../../styles/Admin.css"


const User = () => {
  const [users, setUsers] = useState([])
  const [search, setSearch] = useState("")

  // Get User
  const getUsers = async () => {
    try {
      const res = await axios.get('/api/v1/admin/get-all-users', {
        headers: {
          Authorization: `Bearer ${localStorage.getItem("token")}`
        }
      })
      if (res.data.success) {
        // message.success(res.data.message)
        setUsers(res.data.data)
      } else {
        message.error(res.data.message)
      }
    } catch (error) {
      console.log(error);
      message.error("Something Went Wrong")
    }
  }

  useEffect(() => {
    getUsers();
  }, [])

  return (
    <Layout>
      <div>
        <input className='ps-2 searchInput' placeholder='Search by email' type="search" onChange={(e) => setSearch(e.target.value)} />
      </div>
      <table class="table">
        <thead>
          <tr>
            <th scope="col">Sr no.</th>
            <th scope="col">Username</th>
            <th scope="col">Email</th>
            <th scope="col">Doctor</th>
            <th scope="col">Action</th>
          </tr>
        </thead>
        <tbody>
          {
            users.filter((elm) => elm.email.toLowerCase().includes(search.toLowerCase())).map((user) => {
              return (
                <tr>
                  <th scope="row">{users.indexOf(user) + 1}</th>
                  <td>{user.firstName} {user.lastName}</td>
                  <td>{user.email}</td>
                  <td>{user.isDoctor ? "Yes" : "No"}</td>
                  <td><button className='btn btn-warning'>Block</button></td>
                </tr>
              )
            })
          }
        </tbody>
      </table>
    </Layout>
  )
}

export default User
