import { message } from 'antd'
import axios from 'axios'
import React, { useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { useNavigate } from 'react-router-dom'
import Layout from "../components/Layout"
import { hideLoading, showLoading } from '../redux/features/alertSlice'

const ApplyDoctor = () => {

    const { user } = useSelector(state => state.user);
    const dispatch = useDispatch();
    const navigate = useNavigate();
    const [doctor, setDoctor] = useState({ firstName: "", lastName: "", phone: "", email: "", website: "", address: "", specialization: "", experience: "", feesPerConsultation: "", startTime: "", endTime: "" })

    const submitHandler = async (e) => {
        e.preventDefault();
        try {
            dispatch(showLoading());
            const res = await axios.post('/api/v1/user/apply-doctor', { ...doctor, userId: user._id }, {
                headers: {
                    Authorization: `Bearer ${localStorage.getItem("token")}`
                }
            })
            dispatch(hideLoading());
            if (res.data.success) {
                message.success(res.data.message);
                navigate('/');
            } else {
                message.error(res.data.message)
            }
        } catch (error) {
            dispatch(hideLoading());
            console.log(error);
            message.error('Error Occured in Apply Doctor');
        }
    }

    const handleChange = (e) => {
        setDoctor({ ...doctor, [e.target.name]: e.target.value })
    }

    return (
        <Layout>
            <form onSubmit={submitHandler}>
                <div className="bg-dark p-2 rounded my-4">
                    <h3 className='text-white text-center pt-2'>APPLY AS DOCTOR
                        <i className='ms-3 fa-solid fa-user-doctor text-white'></i>
                    </h3>
                </div>
                <div className="row my-2">
                    <h5>Personal Details</h5>
                    <div className="col-md-4 col-lg-4 col-12 col-sm-12">
                        <div className="mb-3">
                            <label htmlFor="firstName" className="form-label">First Name</label>
                            <input name='firstName' value={doctor.firstName} onChange={handleChange} placeholder="Enter First Name" type="text" className="form-control" id="firstName" />
                        </div>
                        <div className="mb-3">
                            <label htmlFor="email" className="form-label">Email</label>
                            <input name="email" value={doctor.email} onChange={handleChange} placeholder="Enter Email" type="email" className="form-control" id="email" />
                        </div>
                    </div>
                    <div className="col-md-4 col-lg-4 col-12 col-sm-12">
                        <div className="mb-3">
                            <label htmlFor="lastname" className="form-label">Last Name</label>
                            <input name="lastName" value={doctor.lastName} onChange={handleChange} placeholder="Enter Last Name" type="text" className="form-control" id="lastname" />
                        </div>
                        <div className="mb-3">
                            <label htmlFor="website" className="form-label">Website</label>
                            <input name="website" value={doctor.website} onChange={handleChange} placeholder="Enter Website" type="text" className="form-control" id="website" />
                        </div>
                    </div>
                    <div className="col-md-4 col-lg-4 col-12 col-sm-12">
                        <div className="mb-3">
                            <label htmlFor="phone" className="form-label">Phone No</label>
                            <input name="phone" value={doctor.phone} onChange={handleChange} placeholder="Enter Phone Number" type="text" className="form-control" id="phone" />
                        </div>
                        <div className="mb-3">
                            <label htmlFor="address" className="form-label">Address</label>
                            <input name="address" value={doctor.address} onChange={handleChange} placeholder="Enter Address" type="text" className="form-control" id="address" />
                        </div>
                    </div>
                </div>
                <div className="row">
                    <h5>Professional Details</h5>
                    <div className="col-md-4 col-lg-4 col-12 col-sm-12">
                        <div className="mb-3">
                            <label htmlFor="specialization" className="form-label">Specializaion</label>
                            <input name="specialization" value={doctor.specialization} onChange={handleChange} placeholder="Enter Specialization" type="text" className="form-control" id="specialization" />
                        </div>
                        <div className="mb-3">
                            <label htmlFor="startTime" className="form-label">Start Time</label>
                            <input name="startTime" value={doctor.startTime} onChange={handleChange} type="time" className="form-control" id="starttime" />
                        </div>
                    </div>
                    <div className="col-md-4 col-lg-4 col-12 col-sm-12">
                        <div className="mb-3">
                            <label htmlFor="endTime" className="form-label">End Time</label>
                            <input name="endTime" value={doctor.endTime} onChange={handleChange} type="time" className="form-control" id="endTime" />
                        </div>
                        <div className="mb-3">
                            <label htmlFor="experience" className="form-label">Experience</label>
                            <input name="experience" value={doctor.experience} onChange={handleChange} placeholder="Enter Your Experience" type="text" className="form-control" id="experience" />
                        </div>
                    </div>
                    <div className="col-md-4 col-lg-4 col-12 col-sm-12">
                        <div className="mb-3">
                            <label htmlFor="fees" className="form-label">Fees Per Consultation</label>
                            <input name="feesPerConsultation" value={doctor.feesPerConsultation} onChange={handleChange} placeholder="Enter Your Fees" type="text" className="form-control" id="feesPerConsultation" />
                        </div>
                    </div>
                </div>
                <button type="submit" className="btn btn-primary">Apply Doctor</button>
            </form>
        </Layout>
    )
}

export default ApplyDoctor
