import { BrowserRouter, Route, Routes } from "react-router-dom"
import HomePage from "./pages/HomePage"
import Login from "./pages/Login"
import Register from "./pages/Register"
import { useSelector } from "react-redux"
import Spinner from "./components/Spinner"
import ProtectedRoutes from "./components/ProtectedRoutes"
import PublicRoutes from "./components/PublicRoutes"
import ApplyDoctor from "./pages/ApplyDoctor"
import NotificationPage from "./pages/NotificationPage"
import User from "./pages/admin/User"
import Doctor from "./pages/admin/Doctor"
import Profile from "./pages/doctor/Profile"
import BookingPages from "./pages/BookingPages"
import UserAppointmentPage from "./pages/UserAppointmentPage"
import DoctorAppointmentPage from "./pages/doctor/DoctorAppointmentPage"
import LandingPage from "./pages/LandingPage"
import UserProfile from "./pages/UserProfile"
import Support from "./pages/Support"

function App() {
  // Geting our state from redux store
  const { loading } = useSelector((state) => state.alerts);

  return (
    <>
      <BrowserRouter>
        {loading ?
          <Spinner /> :
          <Routes>

            <Route exact path="/" element={
              <PublicRoutes>
                <LandingPage />
              </PublicRoutes>
            } />

            {/* ============================ USER ============================ */}

            <Route exact path="/user-profile" element={
              <ProtectedRoutes>
                <UserProfile />
              </ProtectedRoutes>
            } />
            <Route exact path="/apply-doctor" element={
              <ProtectedRoutes>
                <ApplyDoctor />
              </ProtectedRoutes>
            } />
            <Route exact path="/notification" element={
              <ProtectedRoutes>
                <NotificationPage />
              </ProtectedRoutes>
            } />
            <Route exact path="/appointment" element={
              <ProtectedRoutes>
                <UserAppointmentPage />
              </ProtectedRoutes>
            } />
            <Route exact path="/all-doctors/doctor/book-appointment/:doctorId" element={
              <ProtectedRoutes>
                <BookingPages />
              </ProtectedRoutes>
            } />
            <Route exact path="/all-doctors" element={
              <ProtectedRoutes>
                <HomePage />
              </ProtectedRoutes>
            } />

            {/* ============================ ADMIN ============================ */}

            <Route exact path="/admin/users" element={
              <ProtectedRoutes>
                <User />
              </ProtectedRoutes>
            } />
            <Route exact path="/admin/doctors" element={
              <ProtectedRoutes>
                <Doctor />
              </ProtectedRoutes>
            } />

            {/* ============================ DOCTOR ============================ */}

            <Route exact path="/doctor/profile/:id" element={
              <ProtectedRoutes>
                <Profile />
              </ProtectedRoutes>
            } />

            <Route exact path="/appointment/:doctorId" element={
              <ProtectedRoutes>
                <DoctorAppointmentPage />
              </ProtectedRoutes>
            } />

            {/* ============================ COMMON PAGES ============================ */}

            <Route exact path="/support" element={
              <ProtectedRoutes>
                <Support />
              </ProtectedRoutes>
            } />
            <Route exact path="/login" element={
              <PublicRoutes>
                <Login />
              </PublicRoutes>
            } />
            <Route exact path="/register" element={
              <PublicRoutes>
                <Register />
              </PublicRoutes>
            } />

          </Routes>
        }
      </BrowserRouter>
    </>
  );
}

export default App;
