import { message } from 'antd';
import axios from 'axios';
import React, { useEffect, useState } from 'react'
import Layout from "../../components/Layout"
import "../../styles/Admin.css"

const Doctor = () => {
  const [doctors, setDoctors] = useState([]);
  const [search, setSearch] = useState("");

  const getDoctors = async () => {
    try {
      const res = await axios.get('/api/v1/admin/get-all-doctors', {
        headers: {
          Authorization: `Bearer ${localStorage.getItem("token")}`
        }
      })
      if (res.data.success) {
        // message.success(res.data.message);
        setDoctors(res.data.data);
      } else {
        message.error(res.data.message)
      }
    } catch (error) {
      console.log(error);
      message.error("Something Went Wrong")
    }
  }

  const approveDoctorHandler = async (id) => {
    try {
      const res = await axios.post('/api/v1/admin/approve-doctors', { doctorId: id, userId: doctors.userId, status: "approved" }, {
        headers: {
          Authorization: `Bearer ${localStorage.getItem('token')}`
        }
      })
      if (res.data.success) {
        message.success(res.data.message)
      } else {
        message.error(res.data.message)
      }
    } catch (error) {
      console.log(error);
      message.error("Error Occured")
    }
  }

  const rejectDoctorHandler = async (id) => {
    try {
      const res = await axios.post('/api/v1/admin/reject-doctors', { doctorId: id, userId: doctors.userId, status: "pending" }, {
        headers: {
          Authorization: `Bearer ${localStorage.getItem('token')}`
        }
      })
      if (res.data.success) {
        message.success(res.data.message)
      } else {
        message.error(res.data.message)
      }
    } catch (error) {
      console.log(error);
      message.error("Error Occured")
    }
  }

  useEffect(() => {
    getDoctors();
    // eslint-disable-next-line
  }, [])

  return (
    <Layout>
      <div>
        <input className='ps-2 searchInput' placeholder='Search by email' type="search" onChange={(e) => setSearch(e.target.value)} />
      </div>
      <table class="table">
        <thead>
          <tr>
            <th scope="col">Sr no.</th>
            <th scope="col">Doctor Name</th>
            <th scope="col">Email</th>
            <th scope="col">Mobile No.</th>
            <th scope="col">Doctor</th>
            <th scope="col">Action</th>
          </tr>
        </thead>
        <tbody>
          {
            doctors.filter((elm) => elm.email.toLowerCase().includes(search.toLowerCase())).map((doctor) => {
              return (
                <tr>
                  <th scope="row">{doctors.indexOf(doctor) + 1}</th>
                  <td>{(doctor.firstName + " " + doctor.lastName).toUpperCase()}</td>
                  <td>{doctor.email}</td>
                  <td>{doctor.phone}</td>
                  <td>{doctor.status}</td>
                  <td>
                    {doctor.status === "pending" ?
                      <button onClick={() => approveDoctorHandler(doctor._id)} className='btn btn-success'>Approve</button>
                      :
                      <button onClick={() => rejectDoctorHandler(doctor._id)} className='btn btn-danger'>Reject</button>
                    }
                  </td>
                </tr>
              )
            })
          }
        </tbody>
      </table>
    </Layout>
  )
}

export default Doctor
