import React from 'react';
import Layout from '../components/Layout';
import { message, Tabs } from "antd";
import { useDispatch, useSelector } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import { hideLoading, showLoading } from '../redux/features/alertSlice';
import axios from 'axios';

const NotificationPage = () => {
    const navigate = useNavigate();
    const dispatch = useDispatch();

    const { user } = useSelector(state => state.user)

    const HandleMarkAllRead = async () => {
        try {
            dispatch(showLoading());
            const res = await axios.post('/api/v1/user/get-all-notification', { userId: user._id }, {
                headers: {
                    Authorization: `Bearer ${localStorage.getItem("token")}`
                }
            })
            dispatch(hideLoading());
            if (res.data.success) {
                message.success(res.data.message)
            } else {
                message.error(res.data.message)
            }

        } catch (error) {
            console.log(error);
            message.error("Error in Notification Reading");
        }
    }

    const HandleDeleteAllRead = async () => {
        try {
            dispatch(showLoading());
            const res = await axios.post('/api/v1/user/delete-all-notification', { userId: user._id }, {
                headers: {
                    Authorization: `Bearer ${localStorage.getItem("token")}`
                }
            })
            dispatch(hideLoading());
            if (res.data.success) {
                message.success(res.data.message)
            } else {
                message.error(res.data.message)
                dispatch(hideLoading());
            }
        } catch (error) {
            console.log(error);
            message.error("Something Went Wrong")
            dispatch(hideLoading());
        }
    }

    return (
        <Layout>
            <h3 className='text-center'>Notification Page</h3>
            <Tabs>
                <Tabs.TabPane tab="Unread" key={0}>
                    <div className="d-flex justify-content-end">
                        <button className={`btn btn-primary ${user?.notification === 0 ? 'disabled' : ''}`} onClick={HandleMarkAllRead}>Mark All As Read</button>
                    </div>
                    {
                        user?.notification.length > 0 ? user?.notification.map((notificationMsg) => {
                            return (
                                <div style={{ cursor: "pointer", backgroundColor: "#dbdbdb" }} className='card my-3 p-4' onClick={() => navigate(notificationMsg.data.onClickPath)}>
                                    <div className="card-text">
                                        {notificationMsg.message}
                                    </div>
                                </div>
                            )
                        }) :
                            <div>
                                <h5>No Notification Found</h5>
                            </div>
                    }
                </Tabs.TabPane>
                <Tabs.TabPane tab="Read" key={1}>
                    <div className="d-flex justify-content-end">
                        <button className={`btn btn-danger ${user?.seenNotification === 0 ? 'disabled' : ''}`} onClick={HandleDeleteAllRead}>Delete All Read</button>
                    </div>
                    {
                        user?.seenNotification.length > 0 ? user?.seenNotification.map((notificationMsg) => {
                            return (
                                <div style={{ cursor: "pointer", backgroundColor: "#dbdbdb" }} className='card my-3 p-4' onClick={() => navigate(notificationMsg.data.onClickPath)}>
                                    <div className="card-text">
                                        {notificationMsg.message}
                                    </div>
                                </div>
                            )
                        }) :
                            <div>
                                <h5>No Notification Found</h5>
                            </div>
                    }
                </Tabs.TabPane>
            </Tabs>
        </Layout>
    )
}

export default NotificationPage
