import React from 'react';
import { useNavigate } from 'react-router-dom';
import '../styles/DoctorList.css'

const DoctorList = ({ doctor }) => {

    const navigate = useNavigate();

    return (
        <div className="card" style={{ cursor: 'pointer' }} onClick={() => navigate(`doctor/book-appointment/${doctor._id}`)}>
            <div className="card-header">
                <h4>{`${doctor.firstName} ${doctor.lastName}`}</h4>
            </div>
            <div className="card-body">
                <div className="card-text">
                    <h6>Specialization : {doctor.specialization}</h6>
                    <h6>Experience : {doctor.experience}</h6>
                    <h6>Fees Per Consultation : {doctor.feesPerConsultation}</h6>
                    <h6>Timings : From {doctor.startTime} To {doctor.endTime}</h6>
                </div>
            </div>
        </div>

    )
}

export default DoctorList
