import React, { useState } from 'react';
import "../styles/Login.css"
import axios from 'axios';
import { message } from "antd";
import { Link, useNavigate } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import { hideLoading, showLoading } from '../redux/features/alertSlice';

const Login = () => {
    const navigate = useNavigate();
    const dispatch = useDispatch();

    const [email, setEmail] = useState("")
    const [password, setPassword] = useState("")

    const submitHandler = async (e) => {
        e.preventDefault();
        const regex = /^[^\s@]+@[^\s@]+\.[^\s@]{2,}$/i;
        if (!regex.test(email)) {
            message.error("You entered an invalid email");
        } else if (!password) {
            message.error("Please enter a password")
        } else {
            try {
                dispatch(showLoading());
                const res = await axios.post('/api/v1/user/login', { email, password });
                dispatch(hideLoading());
                if (res.data.success) {
                    localStorage.setItem("token", res.data.token);
                    message.success("Login Successful");
                    console.log(res.data.data.isAdmin)
                    if (res.data.data.isAdmin) {
                        navigate('/admin/doctors')
                    } else if (res.data.data.isDoctor) {
                        navigate(`/appointment/${res.data.data.userId}`)
                        console.log(res.data.data.isDoctor);
                    } else {
                        navigate('/')
                    }
                } else {
                    message.error(res.data.message);
                }
            } catch (error) {
                dispatch(hideLoading());
                console.log(error);;
                message.error("Something Went Wrong")
            }
        }
    }

    return (
        <div className='form-container'>
            <form className='form'>
                <h4 className='text-center'>Login Here</h4>
                <hr />
                <div className="mb-3">
                    <label htmlFor="email" className="form-label">Email address</label>
                    <input onChange={(e) => setEmail(e.target.value)} value={email} type="email" className="form-control" id="email" />
                </div>
                <div className="mb-3">
                    <label htmlFor="password" className="form-label">Password</label>
                    <input onChange={(e) => setPassword(e.target.value)} value={password} type="password" className="form-control" id="password" />
                </div>

                <p className='d-inline me-2'>Not a User?</p><Link to="/register">Register Here</Link>
                <button onClick={submitHandler} type="submit" className="btn btn-primary d-block m-auto mt-2">Login</button>
            </form>
        </div>
    )
}

export default Login
