import axios from 'axios'
import React, { useEffect, useState } from 'react'
import Layout from '../components/Layout'
import { useParams } from 'react-router-dom'
import { message } from 'antd'
import { useSelector } from 'react-redux'

const BookingPages = () => {
    const params = useParams();
    const { user } = useSelector(state => state.user)
    const [doctor, setDoctor] = useState([])
    const [date, setDate] = useState("")
    const [time, setTime] = useState("")

    //* ======================== Fetching Single Doctor ============================
    const getSingleDoctor = async () => {
        try {
            const res = await axios.post('/api/v1/doctor/getDoctorById',
                { doctorId: params.doctorId },
                {
                    headers: {
                        Authorization: `Bearer ${localStorage.getItem("token")}`
                    }
                })
            if (res.data.success) {
                setDoctor(res.data.data);
            } else {
                message.error(res.data.message);
            }
        } catch (error) {
            console.log(error);
            message.error("Error Occured")
        }
    }

    //* ======================== Checking Doctor Availbility Function ============================
    const checkHandler = async () => {
        try {
            const res = await axios.post('/api/v1/user/checkAvailability',
                {
                    doctorId: doctor._id,
                    date: date
                },
                {
                    headers: {
                        Authorization: `Bearer ${localStorage.getItem("token")}`
                    }
                })
            if (res.data.success) {
                message.success(res.data.message);
            } else {
                message.error(res.data.message)
            }
        } catch (error) {
            console.log(error);
            message.error("smthin went wrong")
        }
    }

    //* ======================== Booking Function ============================
    const bookingHandler = async (e) => {
        try {
            const res = await axios.post('/api/v1/user/bookAppointment',
                {
                    userId: user._id,
                    doctorId: params.doctorId,
                    doctorInfo: doctor,
                    userInfo: user,
                    date: date,
                    time: time,
                },
                {
                    headers: {
                        Authorization: `Bearer ${localStorage.getItem("token")}`
                    }
                })
            if (res.data.success) {
                message.success(res.data.message)
            } else {
                message.error(res.data.message)
            }
        } catch (error) {
            console.log(error);
            message.error("Something went wrong");
        }
    }

    useEffect(() => {
        getSingleDoctor();
        //eslint-disable-next-line
    }, [])

    return (
        <Layout>
            <h3 className='text-center'>Booking Page</h3>
            <div className='d-flex justify-content-center p-4'>
                {
                    doctor && (
                        <div className='card p-4' style={{ width: "400px" }}>
                            <h4>{doctor.firstName} {doctor.lastName}</h4>
                            <h4>Fees : {doctor.feesPerConsultation}</h4>
                            <h4>Timings : From {doctor.startTime} To {doctor.endTime}</h4>
                            <input required type="date" onChange={(e) => setDate(e.target.value)} />
                            {/* <input required className='my-2' type="time" onChange={(e) => setTime(e.target.value)} /> */}
                            <button className='btn btn-primary d-block my-2' onClick={checkHandler}>Check Availibility</button>
                            <button className='btn btn-success d-block' onClick={bookingHandler}>Book Now</button>
                        </div>
                    )
                }
            </div>
        </Layout>
    )
}

export default BookingPages
