import React, { useState } from 'react';
import "../styles/Register.css";
import axios from 'axios';
import { useDispatch } from "react-redux"
import { hideLoading, showLoading } from "../redux/features/alertSlice"
import { message } from "antd";
import { Link, useNavigate } from 'react-router-dom';

const Register = () => {
    const navigate = useNavigate();
    const dispatch = useDispatch();

    const initialValues = {
        firstName: "",
        lastName: "",
        email: "",
        password: "",
        confirmPassword: ""
    }
    const [formValues, setFormValues] = useState(initialValues);

    const handleChange = (e) => {
        setFormValues({ ...formValues, [e.target.name]: e.target.value })
    }

    const submitHandler = async (e) => {
        e.preventDefault();
        const regex = /^[^\s@]+@[^\s@]+\.[^\s@]{2,}$/i;
        if (!formValues.firstName || !formValues.lastName || !formValues.email || !formValues.password || !formValues.confirmPassword) {
            message.error("Some fields are empty")
        } else if (!regex.test(formValues.email)) {
            message.error("You entered an invalid email")
        } else if (formValues.password !== formValues.confirmPassword) {
            message.error("Passwords are not matching")
        } else if (formValues.password.length < 5) {
            message.error("Password length must be greater than 5 characters")
        } else {
            try {
                dispatch(showLoading());
                const res = await axios.post('/api/v1/user/register', { firstName: formValues.firstName, lastName: formValues.lastName, email: formValues.email, password: formValues.password });
                dispatch(hideLoading());
                if (res.data.success) {
                    message.success('Registration Successfull')
                    navigate('/login')
                } else {
                    message.error(res.data.message)
                }
            } catch (error) {
                dispatch(hideLoading());
                console.log(error);
                message.error('Something Went Wrong')
            }
        }
    }

    return (
        <>
            <div className='form-container'>
                {/* <pre>{JSON.stringify(formValues, undefined, 2)}</pre> */}
                <form className='form' onSubmit={submitHandler}>
                    <h4 className='text-center'>Register Form</h4>
                    <hr />
                    <div className="mb-3">
                        <label htmlFor="name" className="form-label">First Name</label>
                        <input name='firstName' onChange={handleChange} value={formValues.firstName} type="text" className="form-control" id="name" />
                    </div>
                    <div className="mb-3">
                        <label htmlFor="name" className="form-label">Last Name</label>
                        <input name='lastName' onChange={handleChange} value={formValues.lastName} type="text" className="form-control" id="name" />
                    </div>
                    <div className="mb-3">
                        <label htmlFor="email" className="form-label">Email address</label>
                        <input name='email' onChange={handleChange} value={formValues.email} type="text" className="form-control" id="email" />
                    </div>
                    <div className="mb-3">
                        <label htmlFor="password" className="form-label">Password</label>
                        <input name='password' onChange={handleChange} value={formValues.password} type="password" className="form-control" id="password" />
                    </div>
                    <div className="mb-3">
                        <label htmlFor="confirmpassword" className="form-label">Confirm Password</label>
                        <input name='confirmPassword' onChange={handleChange} value={formValues.confirmPassword} type="password" className="form-control" id="confirmPassword" />
                    </div>

                    <p className='d-inline me-2'>Already a User?</p><Link to="/login">Login Here</Link>
                    <button type="submit" className="btn btn-primary d-block m-auto mt-2">Register</button>
                </form>
            </div>
        </>
    )
}

export default Register
