import React, { useEffect, useState } from 'react'
import { useSelector } from 'react-redux'
import Layout from "../components/Layout"
import axios from 'axios';
import { message } from 'antd';
import { useNavigate } from 'react-router-dom';

const UserProfile = () => {
    const navigate = useNavigate();
    const { user } = useSelector(state => state.user);
    const [userData, setUserData] = useState([])

    const updateHandler = async (e) => {
        e.preventDefault();
        console.log(userData);
        try {
            const res = await axios.post('/api/v1/user/userProfileUpdate', { ...userData, userId: userData._id }, {
                headers: {
                    Authorization: `Bearer ${localStorage.getItem("token")}`
                }
            })
            if (res.data.success) {
                message.success(res.data.message)
                setUserData(res.data.data)
                navigate('/')
            } else {
                message.error(res.data.message)
            }
        } catch (error) {
            message.error("Error Occured")
        }
    }

    const handleChange = (e) => {
        setUserData({ ...userData, [e.target.name]: e.target.value });
    }

    useEffect(() => {
        setUserData(user);
    }, [])

    return (
        <Layout>
            <form onSubmit={updateHandler}>
                {/* DOCTOR PROFILE UPDATE */}
                <div className="bg-dark p-2 rounded my-4">
                    <h3 className='text-white text-center pt-2'>USER PROFILE
                        <i className='ms-3 fa-solid fa-user-doctor text-white'></i>
                    </h3>
                </div>
                <div className="row my-2">
                    <h5 className='text-secondary mb-4 text-center text-lg-start'>Personal Details</h5>
                    <div className="col-md-4 col-lg-4 col-12 col-sm-12">
                        <div className="mb-3">
                            <label htmlFor="firstName" className="form-label">First Name</label>
                            <input value={userData.firstName} name='firstName' onChange={handleChange} placeholder="Enter First Name" type="text" className="form-control" id="firstName" />
                        </div>
                    </div>
                    <div className="col-md-4 col-lg-4 col-12 col-sm-12">
                        <div className="mb-3">
                            <label htmlFor="lastName" className="form-label">Last Name</label>
                            <input value={userData.lastName} name='lastName' onChange={handleChange} placeholder="Enter Last Name" type="text" className="form-control" id="lastName" />
                        </div>
                    </div>
                    <div className="col-md-4 col-lg-4 col-12 col-sm-12">
                        <div className="mb-3">
                            <label htmlFor="email" className="form-label">Email</label>
                            <input value={userData.email} name='email' onChange={handleChange} type="text" className="form-control" id="email" readOnly />
                        </div>
                    </div>
                    <div className="col-md-4 col-lg-4 col-12 col-sm-12">
                        <div className="mb-3">
                            <label htmlFor="email" className="form-label">Password</label>
                            <input name='password' onChange={handleChange} placeholder="***" type="text" className="form-control" id="password" />
                        </div>
                    </div>
                    <div className="col-md-4 col-lg-4 col-12 col-sm-12">
                        <div className="mb-3">
                            <label htmlFor="contact" className="form-label">Contact No.</label>
                            <input value={userData.contact} name='contact' onChange={handleChange} placeholder="Enter Mobile Number" type="text" className="form-control" id="contact" />
                        </div>
                    </div>
                    <div className='text-center my-2'>
                        <button className="btn btn-success">Update Profile</button>
                    </div>
                </div>
            </form>
        </Layout>
    )
}

export default UserProfile
